* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Nunito Sans', sans-serif;
}

#container{
    margin: 50px 180px;
    display: flex;
    flex-direction: column;
}

#heading{
    display: flex;
    flex-direction: row;
    gap: 57.5rem;
    padding: 1px;
}

.gallery-boxes{
    display: flex;
    flex-direction: row;
    padding-top: 30px;
    justify-content: space-between;
    align-items:center;
}

#heading h2{
    color: #1B2E56;
}

.view-btn{
    background: #1B2E56;
    color: white;
    padding: 12px 14px;
    border-radius: 6px;
}

.box1{
    height: 190px;
    width: 250px;
    background: #6a8385;
    border-radius: 14px;
    border-top: 7px solid #1B2E56;
}